  text-align: center;
  grid-template-columns: auto auto;
  grid-template-rows: 4rem auto auto;
}

.Weather[data-v-345df0b4] {
  grid-template-columns: repeat(6, 1fr);;
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 1rem;
  justify-items: center;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  color: #204775;
  text-align: center;
  font-size: 1.3rem;
}
.Weather__icons[data-v-345df0b4] {
  grid-column: 2 / span 2;
  grid-row: 2 / span 2;
  padding: 2rem;
  justify-items: center;
  text-align: center;
  height: 38vh;
}
/*............POSIBLE BACKGROUNDS DEPENDING ON ICON............*/
.Weather__data[data-v-345df0b4]       {
  padding-top: 2rem;
  grid-column: 4 / span 2;
  grid-row: 2 / span 2;
}
.Weather__data--sum[data-v-345df0b4]  {
  font-size: 1.6em;
  margin: 0.7rem 0;
}
.Weather__title[data-v-345df0b4]      { font-family: 'montserratmedium', Arial, sans-serif
}
.Weather__data--temp[data-v-345df0b4] { font-size: 2.2em
}
.Weather__controller[data-v-345df0b4] {
  width: 100%;
  grid-column: 4 / span 2;
  grid-row: 5 / -1;
}
/*............NIGHT............*/
.Weather__night[data-v-345df0b4] {
  background-color: #0B1A2C;
  color: #F2F2F2;
}
.Weather__night .button__small[data-v-345df0b4] {
  background-color: #0B1A2C;
  border: 2px solid #F2F2F2;
  color: #F2F2F2;
}
.Weather__night .button__small[data-v-345df0b4]:hover {
  background-color: #F2F2F2;
  border: 2px solid #F2F2F2;
  color: #0B1A2C;
}
/*............MEDIA QUERIES............*/
